
*{
  margin:0;
  padding:0;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width:100vw;
  height:100vh;
}

.index-page {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 0 0 0 400px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.index-page .btn {
  padding: 10px 20px;
  color: #fff;
  position: absolute;
  left: 10px;
  top: 50px;
  background-color: #2d8cf0;
  border-radius: 4px;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.index-page .btn:hover {
  background-color: #57a3f3;
}
.index-page .btn:focus {
  -webkit-box-shadow: 0 0 0 2px rgba(45, 140, 240, 0.2);
          box-shadow: 0 0 0 2px rgba(45, 140, 240, 0.2);
}
.index-page .btn:active {
  background-color: #2d8cf0;
}
.index-page .tips {
  position: absolute;
  left: 10px;
  top: 150px;
}
.index-page .tips code {
  background: #000;
  color: #fff;
  padding: 10px;
  border-radius: 6px;
}
.index-page .result-box {
  width: 100%;
  height: 100%;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  position: relative;
  overflow-x: hidden;
  overflow-y: visible;
}
.index-page .result-box .box-item {
  width: 180px;
  height: 190px;
  margin: 20px;
  -webkit-transition: all 0.6s ease;
  transition: all 0.6s ease;
  position: absolute;
}
.index-page .result-box .box-item .item-self {
  width: 100%;
  height: 100%;
  border-radius: 4px;
  border: 1px solid #fff;
  background-color: #fff;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  overflow: hidden;
}
.index-page .result-box .box-item .item-self:hover {
  -webkit-box-shadow: 0px 2px 20px -2px rgba(0, 0, 0, 0.5);
          box-shadow: 0px 2px 20px -2px rgba(0, 0, 0, 0.5);
}
.index-page .result-box .box-item .item-self img {
  width: 100%;
}
.index-page .result-box .selected-item .item-self {
  border: 1px solid red;
  border-color: #4162ff;
  -webkit-box-shadow: #4162ff 0px 0px 0px 2px !important;
          box-shadow: #4162ff 0px 0px 0px 2px !important;
}
.bigger {
  width: 100%;
  padding: 0 0 0 100px;
}

.vue-drag-select {
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  overflow: hidden;
  width: 100%;
  height: 100%;
  overflow-y: visible;
  will-change: transform;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.vue-drag-select .select-wrap {
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding-left: 10px;
  min-width: 100%;
  min-height: 100%;
}
.vue-drag-select .select-wrap .wrap-container {
  width: 100%;
  height: 100%;
}
.vue-drag-select-box {
  position: absolute;
  background: rgba(0, 162, 255, 0.4);
  z-index: 99;
  border: 1px dotted #00a2ff;
}
